.project {
  padding: calc(var(--gap) * 2);
  background-color: var(--light);
  border-radius: calc(var(--gap) / 2);
  gap: calc(var(--gap) / 2);
  width: 100%;
}

.project img {
  width: 100%;
  height: auto;
}

.project-name {
  font-weight: bold;
}

.project-details {
  line-height: 1.5rem;
}

.project textarea {
  width: 100%;
}

.project-list.edit .project {
  width: 100%;
}

@media screen and (min-width: 600px) {
  .projects .project-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
